<template>
    <div>
        <div class="menuWrapper">
            <div v-for="(item) in menuData" :key="item.id" :class="['menu-item',{'active':item.id==currentMenuId}]" style="border-bottom:none;" @click="changeMenu(item.id,item.name)">
                {{item.name}}
                <i class="el-icon-delete" @click.stop="deleteMenu(item.id)" v-if="item.name!='其他'"></i>
                <i class="el-icon-edit" @click.stop="updateMenu(item.id,item.name)" v-if="item.name!='其他'"></i>
            </div>
            <!--<div class="menu-item" @click="addMenu">其他</div>-->
            <div class="menu-item" @click="addMenu">添加分类</div>
        </div>

        <el-dialog
                :title="dialogTitle"
                :visible.sync="addMenuVisiable"
                width="30%"
                @close="dialogClose"
                center>
            <el-input
                    ref="newMenu"
                    v-model="newMenu"
                    :placeholder="inputPlaceholder"
                    maxlength="10"
                    @input="checkDuplicate"
            ></el-input>
            <div v-if="menuNameError" style="color:red;margin:10px;">菜单名不可重复</div>
            <div slot="footer" class="dialog-footer">
                <el-button @click="addMenuVisiable = false">取 消</el-button>
                <el-button type="primary" @click="addConfirm" :disabled="menuNameError||!newMenu">确 定</el-button>
            </div>
        </el-dialog>
<!--输入操作密码弹框-->
        <el-dialog
                title="请输入操作密码"
                :visible.sync="passwordVisiable"
                width="30%"
                @close="passwordDialogClose"
                center>
            <el-input
                    ref="password"
                    v-model="password"
                    type="password"
                    :show-password="true"
                    placeholder="请输入操作密码"
                    @keyup.enter.native="passwordConfirm"
                    @input="checkDuplicate"
            ></el-input>
            <div slot="footer" class="dialog-footer">
                <el-button @click="passwordVisiable = false">取 消</el-button>
                <el-button type="primary" @click="passwordConfirm" :disabled="!password">确 定</el-button>
            </div>
        </el-dialog>


    </div>
</template>
<script>
    export default {
        name:'leftMenu',
        data() {
            return {
                currentMenuId:'1',
                inputPlaceholder:'请输入菜单',
                menuData:[],
                newMenu: '',
                dialogTitle:'',
                addMenuVisiable: false,
                menuNameError:false,
                //操作密码弹框
                passwordVisiable:false,
                password:'',
            }
        },
        watch:{
            addMenuVisiable(to){
                if(to){
                    this.$nextTick(()=>{
                        this.$refs.newMenu.focus()
                    })
                }
            },
            passwordVisiable(to){
                if(to){
                    this.$nextTick(()=>{
                        this.$refs.password.focus()
                    })
                }
            },
        },
        methods: {
            passwordDialogClose(){
                this.password='';
                this.passwordVisiable=false;
            },
            passwordConfirm(){
                this.$post('login/operateConfirm', {
                    password: this.password,
                })
                    .then(res => {
                        if (res.code == 200) {
                            if (res.data.desc == 'success') {
                                this.passwordDialogClose();
                                this.$emit('getSubMenu',this.currentMenuId)
                            } else {
                                this.$message.error(res.data.desc)
                            }
                        }
                    })
                    .catch(err => {
                        this.passwordVisiable=false;
                        console.log(err)
                    })

            },
            changeMenu(id,name){
                this.currentMenuId=id;
                if(name=='其他'){
                    console.log('需要密码确认')
                    this.passwordVisiable=true;
                } else{
                    this.$emit('getSubMenu',id)
                }
            },

            deleteMenu(id){
                this.$confirm('确定删除?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    this.$post('menu/deleteMenu',{id})
                        .then(res=>{
                            if(res.code==200){
                                this.$message.success('删除成功');
                                this.getMenuData();
                                this.currentMenuId='1';
                            }
                        })
                        .catch(err=>{
                            console.log(err)
                        })
                }).catch(() => {
                    this.$message({
                        type: 'info',
                        message: '已取消删除'
                    });
                });
            },
            updateMenu(id,name){
                this.dialogTitle='编辑菜单';
                this.inputPlaceholder=name;
                this.currentMenuId=id;
                this.addMenuVisiable=true;

            },
            checkDuplicate(){
                this.menuNameError=false;
                this.menuData.forEach(item=>{
                    if(item.name==this.newMenu){
                        this.menuNameError=true;
                        return false;
                    }
                })
            },
            dialogClose(){
                this.newMenu='';
                this.addMenuVisiable=false;
            },
            getMenuData(){
                this.$get('/menu/getMenu')
                    .then(res=>{
                        if(res.code==200){
                            this.menuData=res.data;
                            this.currentMenuId=res.data[0].id;
                            this.$emit('getSubMenu',this.currentMenuId);
                        }
                    })
                    .catch(err=>{
                        console.log(err)
                    })
            },
            addMenu() {
                this.dialogTitle='添加菜单';
                this.addMenuVisiable = true;

            },
            addConfirm() {
                if(this.dialogTitle=='添加菜单'){
                    this.$post('/menu/addMenu',{menu:this.newMenu})
                        .then(res => {
                            if(res.code===200){
                                this.$message.success('添加成功')
                                this.getMenuData();
                            }
                        })
                        .catch(err => {
                            console.log(err)
                        })
                } else{
                    this.$post('menu/updateMenu',{currentMenuId:this.currentMenuId,newMenu:this.newMenu})
                        .then(res=>{
                            if(res.code==200){
                                this.$message.success('编辑成功')
                                this.getMenuData();
                            }
                        })
                }

                this.addMenuVisiable = false;
            }
        },
        mounted(){
            this.getMenuData();
        }
    }

</script>
<style lang="less" scoped>
    .menuWrapper {
        display: flex;
        flex-direction: column;
        .active{
            background:#adffd2;
        }
        .menu-item {
            width: 150px;
            line-height: 50px;
            border: solid 1px #e6e6e6;
            cursor: pointer;
            position:relative;
            user-select: none;
            border:solid 1px #e0e0e0;
            .el-icon-delete{
                position:absolute;
                right:0;
                bottom:0;
                color:red;
            }
            .el-icon-edit{
                position:absolute;
                right:0;
                top:0;
                color:green;
            }



        }
    }

</style>
