<template>
    <div class="home">
        <left-menu
                class="leftMenu"
                ref="leftMenu"
                @getSubMenu="getSubMenu"
        ></left-menu>
        <sub-menu
                class="subMenu"
                ref="subMenu"
                :currentMenuId="currentMenuId"
                :subMenuData="subMenuData"
        ></sub-menu>
    </div>
</template>

<script>
    import LeftMenu from '../components/LeftMenu'
    import SubMenu from '../components/SubMenu'
    export default {
        name: 'Home',
        components: {
            LeftMenu,
            SubMenu
        },
        data() {
            return {
                currentMenuId: '1',
                subMenuData: []
            }
        },
        methods: {
            getSubMenu(id) {
                this.currentMenuId = id;
                this.$post('menu/getSubMenu', {id})
                    .then(res => {
                        console.log(res)
                        this.subMenuData = res.data;
                    })
                    .catch(err => {
                        console.log(err)
                    })
            }
        },
        beforeRouteLeave (to, from, next) {
            if (to.path === '/artical') {
                from.meta.keepAlive = true
            } else {
                from.meta.keepAlive = false
            }
            next()
        },
    }
</script>
<style lang="less" scoped>
    .home {
        display: flex;
        .subMenu {
            flex: 1;
        }
    }
</style>
