<template>
    <div>
        <div class="subMenuWrapper">
            <div class="menu-item" @click="addMenu">添加子分类</div>
            <div v-for="(item,index) in subMenuData" :key="item.id"
                 :class="['menu-item',{'active':index==currentIndex}]" style="border-bottom:none;"
                 @click="changeMenu(index,item)">
                {{item}}
                <i class="el-icon-delete" @click.stop="deleteSubMenu(index,item)"></i>
                <i class="el-icon-edit" @click.stop="updateMenu(index,item)"></i>
            </div>
        </div>

        <el-dialog
                :title="dialogTitle"
                :visible.sync="addMenuVisiable"
                width="30%"
                @close="dialogClose"
                center>
            <el-input
                    ref="newMenu"
                    v-model="newMenu"
                    type="textarea"
                    :placeholder="inputPlaceholder"
                    maxlength="50"
                    rows="3"
                    @input="checkDuplicate"></el-input>
            <div v-if="menuNameError" style="color:red;margin:10px;">菜单名不可重复</div>
            <div slot="footer" class="dialog-footer">
                <el-button @click="addMenuVisiable = false">取 消</el-button>
                <el-button type="primary" @click="addConfirm" :disabled="menuNameError||!newMenu">确 定</el-button>
            </div>
        </el-dialog>


    </div>
</template>
<script>
    export default {
        name: 'subMenu',
        data() {
            return {
                currentIndex: '',
                inputPlaceholder: '请输入菜单',
                newMenu: '',
                oldMenu:'',
                dialogTitle: '',
                addMenuVisiable: false,
                menuNameError: false,
                currentMenu: ''

            }
        },
        watch:{
            addMenuVisiable(to,from){
                if(to){
                    this.$nextTick(()=>{
                        this.$refs.newMenu.focus();
                    })
                }
            }
        },
        props: ['subMenuData', 'currentMenuId'],
        methods: {
            changeMenu(index,item) {
                this.currentIndex = index;
                this.$router.push({
                    path:'artical',
                    query:{
                        title:item
                    }
                })
            },
            deleteSubMenu(index,item) {
                this.$confirm('确定删除?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    this.subMenuData.splice(index, 1);
                    const data={
                        id: this.currentMenuId,
                        newSubMenu: this.subMenuData.join('|'),
                        title:item
                    }
                    this.$post('menu/deleteSubMenu', data)
                        .then(res => {
                            console.log(res)
                            if (res.code == 200) {
                                this.$message.success('删除成功');
                                this.getMenuData();
                            }
                        })
                        .catch(err => {
                            console.log(err)
                        })
                })
                    .catch(() => {
                        console.log('!')
                        this.$message({
                            type: 'info',
                            message: '已取消删除'
                        });
                    });
            },
            updateMenu(index, item) {
                this.dialogTitle = '编辑菜单';
                this.newMenu = item;
                this.oldMenu=item;
                this.currentMenu = item;
                this.addMenuVisiable = true;
            },
            checkDuplicate() {
                console.log(this.newMenu)
                this.menuNameError = false;
                this.subMenuData.forEach(item => {
                    if (item.name == this.newMenu) {
                        this.menuNameError = true;
                        return false;
                    }
                })
            },
            dialogClose() {
                this.newMenu = '';
                this.addMenuVisiable = false;
            },
            addMenu() {
                this.dialogTitle = '添加菜单';
                this.addMenuVisiable = true;
            },
            addConfirm() {
                if (this.dialogTitle == '添加菜单') {
                    this.subMenuData.unshift(this.newMenu);
                    this.$post('/menu/addSubMenu', {id: this.currentMenuId, subName: this.subMenuData.join('|')})
                        .then(res => {
                            if (res.code === 200) {
                                this.$message.success('添加成功')
                                this.getMenuData();
                            }
                        })
                        .catch(err => {
                            console.log(err)
                        })
                } else {
                    this.subMenuData.splice(this.currentIndex, 1, this.newMenu);
                    const data={
                        id: this.currentMenuId,
                        newSubMenu: this.subMenuData.join('|'),
                        title:this.newMenu,
                        oldTitle:this.oldMenu
                    }
                    this.$post('menu/updateSubMenu', data)
                        .then(res => {
                            if (res.code == 200) {
                                this.$message.success('编辑成功')
                            }
                        })
                }

                this.addMenuVisiable = false;
            }
        }
    }

</script>
<style lang="less" scoped>
    .subMenuWrapper {
        margin: 0 20px;
        display: flex;
        flex-direction: column;
        .active {
            background: #adffd2;
        }
        .menu-item {
            width: 100%;
            line-height: 50px;
            border: solid 1px #e6e6e6;
            cursor: pointer;
            position: relative;
            user-select: none;
            .el-icon-delete {
                position: absolute;
                right: 0;
                bottom: 0;
                color: red;
            }
            .el-icon-edit {
                position: absolute;
                right: 0;
                top: 0;
                color: green;
            }

        }
    }

</style>
